* {
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: Courier New, Courier, monospace;
}

a {
  text-decoration: none;
}

.anchor {
  scroll-margin-top: 64px;
}

.text-shadow {
  text-shadow: 1px 1px 3px #fffdd0;
  transition: text-shadow 50ms ease-in;
}

.text-shadow:hover {
  text-shadow: 1px 1px 3px #0006;
}

.dropdown-item_my {
  white-space: nowrap;
  color: #007bff;
  background-color: #0000;
  border: 0;
  width: 100%;
  padding: .25rem 1.5rem;
  display: block;
}

.hidden {
  display: none;
}

nav {
  z-index: 6;
  letter-spacing: .1rem;
  background-image: linear-gradient(#fff 85%, #0000);
  justify-content: space-between;
  height: 70px;
  font-family: Amatic SC;
  font-size: 23px;
  display: flex;
  top: 0;
  position: sticky !important;
}

nav .logo {
  width: 60px;
  height: 60px;
  margin-left: 30px;
}

nav .links {
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 80%;
  display: flex;
}

main {
  z-index: 1;
  flex-direction: column;
  align-items: center;
  display: flex;
}

main .my-container {
  z-index: 2;
  display: relative;
}

main .my-container .grain {
  opacity: .25;
  z-index: 0;
  background-image: url("grain.7fcac8b9.svg");
  width: 100%;
  height: 100%;
  position: absolute;
}

#visual-slam {
  background-image: linear-gradient(to right, #fff, #fff 50%, #87ceeb80);
  justify-content: center;
  width: 100%;
  font-family: Cabin Sketch;
  font-size: 18px;
  font-style: italic;
  display: flex;
}

#visual-slam .stuff {
  flex-direction: row;
  justify-content: space-between;
  width: 510px;
  padding: 25px 0;
  display: flex;
}

#visual-slam .stuff .desc {
  max-width: 250px;
}

#visual-slam .stuff .desc span {
  text-shadow: 1px 1px 3px #fffdd0;
  vertical-align: middle;
}

#whoami {
  background-color: #ffebcd;
  background-image: linear-gradient(to left, #fff, #ffebcd 40%, #ffebcd);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 25px 0;
  display: flex;
  position: relative;
}

#whoami .slideshow {
  flex-direction: row;
  justify-content: center;
  height: 176px;
  display: flex;
  position: relative;
}

#whoami .slideshow .boyowa {
  border-radius: 50%;
  width: auto;
  height: 176px;
  position: absolute;
}

#whoami .desc {
  z-index: 1;
  max-width: 400px;
  padding: 0 15px;
  font-family: Cabin Sketch;
}

#whoami .show-more, #whoami .show-less {
  color: #db7093;
  text-align: center;
  cursor: pointer;
  white-space: nowrap;
  padding-top: 10px;
  display: block;
}

#whoami .show-more:hover, #whoami .show-less:hover {
  text-decoration: underline;
}

#whoami .show-less {
  font-size: 15px;
  font-weight: normal;
}

#whoami .more-to-show {
  display: none;
}

#whoami .text-break {
  text-align: center;
  margin: 0 auto;
  padding-top: 10px;
  font-size: 30px;
  display: block;
}

#connect {
  background-image: linear-gradient(to bottom right, #fff, #fff 50%, #87ceeb80);
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 25px 0;
  display: flex;
}

#connect .connect {
  width: 300px;
}

#connect .connect .title {
  letter-spacing: .3rem;
  text-align: center;
  width: 100%;
  padding-bottom: 10px;
  font-family: Amatic SC;
  font-size: 24px;
  font-weight: bold;
  display: block;
}

#connect .connect form {
  flex-direction: column;
  display: flex;
}

#connect .connect form > * {
  margin: 1px 0;
}

#connect .connect form button:hover {
  cursor: pointer;
}

#connect .connect form textarea {
  resize: vertical;
}

#connect .connect .form-input {
  background-image: linear-gradient(to bottom right, #fff, #fff 50%, #87ceeb33);
  border: 1px solid #87ceeb;
  padding: 7px 3px;
  font-family: Courier New, Courier, monospace;
}

#connect .connect .form-input:hover {
  background-color: #87ceeb33;
  background-image: none;
  transition: all .2s;
}

#connect .connect .form-input:focus {
  outline: none;
}

#connect .connect .first-last-name {
  grid-template-columns: 50% 50%;
  display: grid;
}

#connect .connect .first-last-name .first {
  margin-right: 1px;
}

#connect .connect .first-last-name .last {
  margin-left: 1px;
}

#connect .connect .socials {
  flex-direction: row;
  justify-content: flex-start;
  display: flex;
}

#connect .connect .socials a {
  margin-top: 5px;
  display: block;
}

#connect .connect .socials a .icon {
  width: 35px;
  height: 35px;
  margin-right: 6px;
}

#connect .connect .socials a .icon:hover {
  transition: all .2s;
  transform: scale(1.1);
}

#connect .connect .socials a .icon:active {
  background-color: #87ceeb;
  border-radius: 6px;
}

.footer {
  color: #f5f5f5;
  background-color: #1a0000;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100px;
  display: flex;
}

.footer #year {
  margin: 0 3px;
}

@media only screen and (width >= 621px) {
  #nav-dropdown {
    display: none;
  }
}

@media only screen and (width <= 620px) {
  #nav-horizontal {
    display: none;
  }

  #nav-dropdown {
    padding: 0;
  }

  #nav-dropdown .dropdown-toggle {
    padding: 0 0 10px;
  }

  nav {
    height: 60px;
  }

  nav .logo {
    width: 50px;
    height: 50px;
    margin-top: 5px;
    margin-left: 5px;
  }

  #visual-slam .stuff {
    flex-direction: column;
    align-items: center;
  }

  #visual-slam .stuff .desc {
    margin-top: 15px;
  }

  #connect .connect {
    width: 270px;
  }
}

